import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
Vue.use(Vuex);
const state = {
  url:'http://10.67.241.218/yhportal/api/business/acticleList?',
  url1:'http://10.67.241.218/yhportal/api/business/editfavorite'
};
const mutations = {

};
const getters = {
  count: function (state) {
    return state.count + 100
  }
};
const actions = {
  subAjax(context,options){
    axios({
      method:options.method,
      url: options.url,
      data: options.param
    }).then((res)=>{
      options.callback && options.callback(res);
    },err =>{
      console.log(err)
    })
  }
};
export default new Vuex.Store({
  state,
  mutations,
  actions
})



